import { abab, aaa, repeatNumber, hasNNumber, ababab, isYiJing, isPureYijing  } from 'app/helpers/number'


const nDigits = [...Array(10).keys()].map(e => ({
    name: `${e} Num`,
    func: (n) => hasNNumber(n, `${e}`, 3)
}))

export const NumberCategory = [
    {
      name: 'ALL',
      func: (n) => true
    },
    {
      name: 'ABAB',
      func: abab
    },
    {
      name: 'AAA',
      func: aaa
    },
    {
        name: 'ABABAB',
        func: ababab
    },
    {
        name: 'REPEAT',
        func: repeatNumber
    },
    {
        name: 'YiJing',
        func: isYiJing
    },
    {
      name: 'Pure YiJing',
      func: isPureYijing
  },
    ...nDigits
  ]


